<script>
import Slide from '@/components/home/Slide.vue'
import Category from '@/components/home/Category.vue'
import ProductBuyNow from '@/components/home/ProductBuyNow.vue'
import ProductSellNow from '@/components/home/ProductSellNow.vue'
import BuySell from '@/components/home/BuySell.vue'
import MapMenu from '@/components/home/Map.vue'
import { Component, Vue } from "vue-property-decorator";
export default {
    name: "home-page",
    components: {
        Slide,
        Category,
        ProductBuyNow,
        ProductSellNow,
        BuySell,
        MapMenu,

    },
    data() {
        return {
            tabs: null,
            // dialog: false,
        }
    },
    async created() {

        if (this.$route.query.tab == 1) {
            this.tabs = 0
        } else if (this.$route.query.tab == 2) {
            this.tabs = 1
        } else {}
        this.dialog = true;
    }
};
